.header {
  width: 100%;
  background: #c4c4c4;
  min-height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media screen and (max-width: 767px) {
  .header {
    min-height: 72px;
  }
}

@media screen and (max-width: 425px) {
  .header {
    min-height: 56px;
  }
}
